export const getGroupName = (selectEvent: number, selectGroup: number): string => {
  if (!GroupNames[selectEvent] || !GroupNames[selectEvent][selectGroup]) {
    return "Undefined";
  }
  return `${getGroupKey(selectGroup)}:${GroupNames[selectEvent][selectGroup]}`;
};

