export const RuleKey = (ruleKey: number): string => {
  for (const key in Rules) {
    if (Rules[key as keyof typeof Rules] === ruleKey) {
      return key;
    }
  }
  return "Undefined";
};

export const RuleName = (rule: number): string => {
  const ruleKey = RuleKey(rule);
  const parts = ruleKey.split("_");
  if (parts.length === 1) {
    return parts[0];
  } else {
    return parts[1];
  }
};
